.modal {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 20px;
  top: 0;
  left: 0;
  overflow-y: auto;
  width: 100vw;
  height: 100vh;
}

.modal-body {
  min-width: 280px;
  min-height: 365px;
  width: 100%;
  background-color: #fff;
  border: 2px solid #ff6b08;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: 26px;
  transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  position: relative;
  overflow-y: auto;

  @media screen and (min-width: 320px) {
    max-width: 450px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
  }
  @media screen and (min-width: 768px) {
    max-width: 600px;
    min-height: 400px;
    padding: 10px;
    overflow: hidden;
  }
}

body.team-open {
  height: 100vh;
  overflow: hidden;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 12px;
  width: 30px;
  height: 30px;
  padding-bottom: 3px;  
  &:hover,
  &:focus {
      transform: scale(1.25);

    align-items: center;
  }
}



.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.team-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
  gap: 25px;
}

.modal-team-title {
  font-size: 50px;
  letter-spacing: 2px;
  text-align: center;
  margin-bottom: 20px;
}

.team-one {
  width: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  text-align: center;
}

.team-photo:hover{
  transform: scale(1.15);
box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
margin-bottom: 5px;
}

.team-photo {
  width: 120px;
  height: 120px;
  border-radius: 35%;
  border: 2px solid #ff6b08;
  transition-delay: 50ms;
}


